import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'

Vue.use(VueRouter)
console.log('base', process.env)
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // 登录状态拦截
  console.log('路由拦截')
  next()
})

router.afterEach(() => {
  window.scrollTo(0, 0)
})

export default router
