
export default function (instance) {
  // 请求拦截
  instance.interceptors.request.use((config) => {
    // 请求参数打印
    console.log('============= request')
    console.log(config)
    return config
  })

  // 响应拦截
  instance.interceptors.response.use((res) => {
    // 响应结果打印
    console.log(res)
    console.log('response ====================')
    return res
  })
}
