
import { Toast } from 'vant'

export default function (instance) {
  // 请求拦截
  instance.interceptors.request.use((config) => {
    Toast.loading({
      message: '加载中...',
      forbidClick: true,
      duration: 0
    })
    return config
  })

  // 响应拦截
  let st = null
  instance.interceptors.response.use((res) => {
    // 防止多个请求并发导致loading抖动
    if (st) {
      clearTimeout(st)
    }
    st = setTimeout(() => {
      Toast.clear()
    }, 500)
    return res
  }, error => {
    console.log('error', error)
  })
}
